import React from 'react';
import './App.css'
import { useState } from 'react';
import Main from './components/Main';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { ThemeProvider } from '@emotion/react';
import { Box, CssBaseline, Typography ,createTheme,Switch} from '@mui/material';
import {AppBar,Toolbar,IconButton} from '@mui/material';

function App() {

  const [mode, setMode] = useState("light")
  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
    },
  });

  const lightTheme = createTheme({
    palette: {
      mode: 'light',
    },
  });

  const selectedTheme = mode === "dark" ? lightTheme : darkTheme;

  return (
    <div className="App">

      <ThemeProvider theme={selectedTheme}>
        <CssBaseline />
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'flex-end' }}>
          <Switch onChange={() => setMode(mode === "light" ? "dark" : "light")} />
        </Box>
         <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static" style={{ marginBottom: "60px" }} >
          <Toolbar style={{ display: "flex", justifyContent: "center" }}>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2}}
            >
              <Typography variant='h3'>Rick and Morty</Typography>
            </IconButton>
          </Toolbar>
        </AppBar>
      </Box>
        
        <BrowserRouter>
          <Routes>
            <Route path="/" element={<Main />} />
          
          </Routes>
        </BrowserRouter>
      </ThemeProvider>
    </div>

  )
}

export default App;