import React, { useCallback, useEffect, useState } from 'react'
import MUIDataTable from 'mui-datatables';
import { Avatar } from '@mui/material';


function Charcter({character, setCharacter}) {
  const[page,setPage]=useState(1);
  const [filter, setFilter] = useState({
    status: "",
    species: "",
    gender: ""
  })
  
  const columns = [{
    name: "id",
    label: "Id",
    options: {
      filter: false,
      sort: true,
    }
  },
  {
    name: "name",
    label: "Name",
    options: {
      filter: false,
      sort: false,
    }
  },
  {
    name: "image",
    label: "Image",
    options: {
      filter: false,
      sort: false,
      customBodyRender: (value) => {
        return <Avatar src={value} alt="Character" width="100" />;
      }
    }
  },
  {
    name: "status",
    label: "Status",
    options: {
      filter: true,
      sort: false,
    }
  },
  {
    name: "species",
    label: "species",
    options: {
      filter: true,
      sort: false,
    }
  },
  {
    name: "gender",
    label: "gender",
    options: {
      filter: true,
      sort: false,
    }
  },
  ]

  const handleFilter =useCallback(async() => {
    try{
    const fetchApiResponse = await fetch(`https://rickandmortyapi.com/api/character/?page=${page}&status=${filter.status}&species=${filter.species}&gender=${filter.gender}`)
    const responseData = await fetchApiResponse.json();
    setCharacter([...responseData.results]);}
    catch(e){
      console.log("error");
    }} ,[filter,setCharacter,page]) 
 

  const options = {
    filterType: 'dropdown',
    count: 826,
    rowsPerPage:20,
    rowsPerPageOptions:[50],
    serverSide: true,
    onTableChange: (action, tableState)=>{
      if(action === 'changePage'){
        setPage( tableState.page + 1);
        handleFilter(page);
      }
    },

    onFilterChange: (changedColumn, filterList) => {
      if(changedColumn === 'status'){
        setFilter({...filter, status: filterList[3]});
      }else if(changedColumn === 'species'){
        setFilter({...filter, species: filterList[4]})
      }else{
        setFilter({...filter, gender: filterList[5]})
      }
      handleFilter();
    }
  };


  useEffect(()=> {
    handleFilter();
  },[handleFilter])


  return (
    <MUIDataTable
      title={"Characters"}
      data={character}
      columns={columns}
      options={options}
    />

  )
}
export default Charcter