import React, { useEffect } from 'react'
import Charcter from './Charcter';

function Main() {
  const [character, setCharacter] = React.useState([]);

  const getCharachters = async () => {
    try {
      const fetchApiResponse = await fetch(`https://rickandmortyapi.com/api/character/`);
      const responseData = await fetchApiResponse.json();
      setCharacter([...responseData.results]);
    }
    catch (e) {
      console.log("error");
    }
  }

  useEffect(() => {
    getCharachters();
  }, [])

  return (
    <div>

      <Charcter character={character} setCharacter={setCharacter} />
    </div>
  )
}

export default Main